body {
  background: linear-gradient(to bottom, #e6f0ff 0%, #6699ff 100%);
  background-attachment: fixed;
  background-size: cover;
  height: 100vh;
  display: grid;
  grid-template-rows: repeat(7, max-content);
  grid-template-columns: 3fr 5fr;
  grid-template-areas: "header header" "navZone navZone" "asideZone asideZone" "mainZone mainZone" "skills skills" "projects projects" "footer footer";
  grid-auto-rows: max-content;
  color: #030376;
  margin: 2%;
}
@media screen and (min-width: 576px) and (max-width: 768px) {
  body {
    grid-template-columns: 1fr 2fr;
    grid-template-rows: repeat(6, max-content);
    grid-template-areas: "header header" "navZone asideZone" "navZone mainZone" "navZone skills" "projects projects" "footer footer";
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  body {
    grid-template-columns: 2fr 3fr 2fr;
    grid-template-rows: repeat(5, max-content);
    grid-template-areas: "header header header" "navZone mainZone asideZone" "navZone mainZone skills" "projects projects projects" "footer footer footer";
  }
}
@media screen and (min-width: 1024px) {
